.container {
	display: flex;
	flex-wrap: wrap;
	margin: 0 10px; }
.full {
	width: 100%; }
.half {
	width: 50%; }
.quarter {
	width: 25%; }

@media (min-width: $tablet-min) {

	.container {
		width: $tablet-container;
		margin: 0 auto; } }








