body {
	font-family: $font-open;
	font-size: 14px;
	color: $black;
	transition: all .3s ease; }

ul {
	margin: 0;
	padding: 0;
	list-style: none;
	li {
		margin: 0;
		padding: 0; } }

h1 {
	font-family: $font-quick;
	font-weight: 400;
	font-size: 20px;
	color: #f00; }

a {
	text-decoration: none;
	color: $black;
	transition: color .3s ease;
	&:visited {
		color: $black; }
	&:hover {
		color: $darkgray;
		text-decoration: none; } }



